  font-family: "Young Style", sans-serif;
  font-family: "Young Style Underlines", sans-serif;
  src: url("https://fonts.cdnfonts.com/css/young-style") format("woff2");
}
#app {
  overflow-x: hidden;
}

#header {
  text-align: center;
}
#header #banner {
  min-width: 1000px;
  position: relative;
  top: 0;
  border: outset #4a3f35 4px;
}
#header #title {
  font-family: "Young Style", sans-serif;
  font-size: 100px;
  color: white;
  margin-top: -200px;
  position: relative;
  text-shadow: black 2px 2px;
}
#header h2 {
  color: #4a3f35;
  font-family: "Young Style", sans-serif;
  margin-top: -50px;
}
#header #our-email {
  color: #bcaaa4;
}

form {
  text-align: center;
  display: flex;
  flex-direction: column;
}
form label {
  display: none;
}
form * {
  padding: 8px;
  margin: 5px;
}
form #submit {
  align-self: center;
  width: 20%;
  background-color: #4a3f35;
  font-family: "Young Style", sans-serif;
  border-radius: 0.5em;
  font-size: 20px;
  color: #bcaaa4;
  border: #4a3f35 2px;
}
form #submit:hover {
  background-color: #bcaaa4;
  color: #fff7e6;
